import React,{useState} from 'react'
import logo from '../assets/logoo.png';
import emailjs from 'emailjs-com';

function Navbar() {

    const [show, setshow] = useState(false);
    function sendEmail(e) {
        e.preventDefault();    //This is important, i'm not sure why, but the email won't send without it
    
        emailjs.sendForm('gmail', 'template_t0ndiwg', e.target, 'user_bc1j0p7BS8zmTpSOPVeWY')
          .then((result) => {
              window.location.reload()  //This is if you still want the page to reload (since e.preventDefault() cancelled that behavior) 
          }, (error) => {
              console.log(error.text);
          });
      }

    return (
        <div className="nav">
            <ul>
            <li className="logol"><img src={logo} /><p>Soft <br></br> Cell</p></li>
            {!show&& <li className="logos"><img src={logo} /><p>Soft <br></br> Cell</p></li>}
                <li>
                {!show&& <div className="contact" onClick={()=>setshow(true)}>SAY HELLO!</div>}
                {show&&<div className="contact-page">
                <button className="close" onClick={()=>setshow(false)} >Close</button>
        <p className="title">To Say <br></br> Hello!</p>
        <form className="contact-form" onSubmit={sendEmail}>
        
        <input type="text" name="from_name" placeholder="Your Name" />
        
        <input type="number" name="contact_number" placeholder="Your Contact" />

        <input type="email" name="from_email" placeholder="Your Email" />
        
        <input type="text" name="subject" placeholder="Message" />
        
        
        <button type="submit" value="Send" className="submit" >send</button>
    </form>
    

</div>}
                </li>
            </ul>
        </div>
    )
}

export default Navbar
