import React from 'react'
import CasesCard from './CasesCard';
import office from '../assets/office.jpg';
import paath from '../assets/paath.jpeg';
import impulse from '../assets/impulse.jpeg';
import mithila from '../assets/mithila.jpeg';
import ideal from '../assets/ideal.jpg';
import masaala from '../assets/masala.jpg'

function Cases() {
    return (
        <div className="cases">
            <p className="title">Cases</p>
            <CasesCard name="Mithila Ride" work="Digital Marketing" cat="Travels" url={mithila} detail="It's a Darbhanga based cab service which we help us to expands throughout in Bihar." />
            <CasesCard name="Impulse Kota" work="Digital Marketing" cat="Institute" url={impulse} detail="It's a premier Instute of Darbhanga, Bihar which helps students in preparing NEET / JEE." />
            <CasesCard name="Paathshaala-An IITian Initiative" work="Digital Marketing" cat="Institute" url={paath}  detail="It's a premier Instute of Darbhanga, Bihar which helps students in preparing NEET / JEE." />
            <CasesCard name="Ideal Path" work="Digital Marketing" cat="Institute" url={ideal}  detail="It's a premier Instute of Darbhanga, Bihar which helps students in preparing NEET / JEE." />
            <CasesCard name="Bollywood Masaala" work="Digital Marketing" cat="Resturant" url={masaala} detail="It's a Bollywood theme based resturant in Darbhanga." />
        </div>
    )
}

export default Cases
