import React from 'react'
import atul from '../assets/atul.jpeg';
import ashish from '../assets/ashish.jpeg';
import suraj from '../assets/suraj.jpeg';
import om from '../assets/om.jpeg';
function About() {
    return (
        <div className="about">
            <p className="title" >We are <br></br> Soft Cell</p>
            <div className="images">
                <div className="image-holder">
                    <div className="image"><img src={atul}/></div>
                    <p className="name">Atul Kumar</p>
                    <p>Founder, Digital Marketing Expert</p>
                </div>
                <div className="image-holder">
                    <div className="image"><img src={ashish}/></div>
                    <p className="name">Ashish Kumar</p>
                    <p>Co-Founder, Web Developer</p>
                </div>
                <div className="image-holder">
                    <div className="image"><img src={suraj}/></div>
                    <p className="name">Suraj Kumar jha</p>
                    <p>Marketing Expert</p>
                </div>
                <div className="image-holder">
                    <div className="image"><img src={om}/></div>
                    <p className="name">Om Prakash Singh</p>
                    <p></p>
                </div>
            </div>
        </div>
    )
}

export default About
