import React from 'react';
import {  } from 'react-icons/fa';


function ServiceCard(props) {
    return (
        <div className="service-card">
        
        <img src={props.src}/>
        <p className="title">{props.title}</p>
        <p className="detail">{props.detail}</p>
        </div>
    )
}

export default ServiceCard
