import React from 'react';
import {Rectangle, Circle, Ellipse, Line, Polyline, CornerBox, Triangle} from 'react-shapes';
import left from '../assets/left.png';
import right from '../assets/right.png';
import small from '../assets/smallgrass.png'
import grass from '../assets/grass.png'
export default class  Top extends React.Component {
    render(){
    return (
        <div className="graphic">
            <div className="board">WE ARE SOFT CELL</div>
            <div className="foot">
            <div className="left-foot"><img src={small} /></div><div className="right-foot"><img src={small} /></div>
            
            </div>
            <div className="football">
            <img src={left} className="left"/>
            <div className="ball-container">
            <Circle className="ball" r={25} fill={{color:'#FEC441'}} stroke={{color:'black'}} strokeWidth={3} />
            </div>
             <img src={right} className="right" />
            </div>
        </div>
    )
    }
}
