import React from 'react'
import hero from '../assets/office.jpg';

import { FaArrowCircleUp } from 'react-icons/fa';
class Hero extends React.Component {
    render(){
    return (
        <div className="hero">
        <div className="image">
            <img src={hero}/>
            </div>
            <p className="herob" >
            Do You Want to be <br></br> a hero Brand ?
            <div className="hello">
                <div className="say">
                    Just Say Us a Hello!
                </div>
                <div>
               <FaArrowCircleUp className="arrow" />
               </div>
            </div>
            </p>
            
        </div>
    )
    }
}

export default Hero;
