import React from 'react'
import {AiFillCaretDown, AiFillCaretUp} from 'react-icons/ai'
import { useState } from 'react'

function CasesCard(props) {
    const [show, setshow] = useState(false)
    return (
        <div>
        <div className="case-card" onClick={()=>setshow(!show)}  >
            <p className="name">{props.name}</p>
           <div><p className="type">What we do</p><p className="detail">{props.work}</p></div>
           <div><p className="type">Category</p><p className="detail">{props.cat}</p></div>
           <div>{!show&&<AiFillCaretDown/>}{show&&<AiFillCaretUp/>}</div>
        </div>
        {show&&<div className="more-detail">
          <img src={props.url} />
          <div className="details">
              <p>{props.detail}</p>
          </div>
        </div>}
        </div>
    )
}

export default CasesCard
